* {
  box-sizing: border-box;
  border: none;
  outline: none;
  margin: 0;
  padding: 0;
}

body {
  background-color: #000;
  font-size: 16px;
}

.container {
  max-width: 1280px;
}

.photos {
  flex-wrap: wrap;
  display: flex;
}

.photo {
  width: 20%;
  z-index: 0;
  transition: box-shadow .5s, transform .5s;
  position: relative;
}

.photo:hover {
  z-index: 1;
  transform: perspective(400px)scale(120%)rotateY(15deg);
  box-shadow: 0 0 24px 5px #000;
}

.search-form {
  width: 100%;
  justify-content: center;
  align-items: center;
  padding-top: 16px;
  display: flex;
}

.search-input {
  width: 30%;
  text-align: center;
  color: #fff;
  background-color: #000;
  border: 1px solid #ff0;
  border-radius: 10px;
  padding: 10px 50px;
  display: block;
}

header {
  width: 100%;
  height: 50px;
  background-color: rgba(84, 84, 194, .714);
}

